.block-inner {
  // .block-inner__slider

  &__slider {
  }

  // .block-inner__wrapper

  &__wrapper {
  }

  // .block-inner__slide

  &__slide {
    display: flex;
    height: auto;
    position: relative;
    padding: 120px 0px;
    @media (max-width: 768px) {
      padding: 60px 0px;
    }
    &::before {
      content: "";
      background: linear-gradient(47deg, rgba(17, 17, 17, 0.3) 41.77%, rgba(119, 119, 119, 0.3) 100%);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      z-index: 3;
    }
    &::after {
      content: "";
      background: rgba(17, 17, 17, 0.3);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      z-index: 4;
    }
    &.slide1 {
      &::before {
        content: "";
        background: linear-gradient(113deg, rgba(119, 119, 119, 0.3) 0%, rgba(17, 17, 17, 0.3) 100%);
      }
    }
    &.slide2 {
      .block-inner__left {
        flex: 0 1 863px;
        @media (max-width: 992px) {
          flex: 1 1 auto;
        }
      }
    }
    &.slide3 {
      .block-inner__left {
        flex: 0 1 924px;
        @media (max-width: 992px) {
          flex: 1 1 auto;
        }
      }
    }
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  // .block-inner__body

  &__body {
    position: relative;
    z-index: 10;
    display: flex;
    justify-content: space-between;
    gap: 50px 30px;
    @media (max-width: 992px) {
      flex-direction: column;
      justify-content: start;
    }
    @media (max-width: 768px) {
      gap: 20px;
    }
    ul {
      flex: 0 1 437px;
      @media (max-width: 992px) {
        flex: 1 1 auto;
        max-width: 437px;
      }
      li {
        border-bottom: 1px solid rgba(255, 255, 255, 0.4);
        padding: 0px 0px 25px 0px;
        margin: 0px 0px 25px 0px;
        &:last-child {
          margin: 0px 0px 0px 0px;
          padding: 0px 0px 0px 0px;
          border-bottom: none;
        }
        @media (max-width: 768px) {
          margin: 0px 0px 10px 0px;
          padding: 0px 0px 10px 0px;
        }
      }
    }
  }

  // .block-inner__left

  &__left {
    flex: 0 1 781px;
    @media (max-width: 992px) {
      flex: 1 1 auto;
    }
    p {
      font-weight: 350;
      font-size: 20px;
      line-height: 160%;
      color: #fff;
      margin: 40px 0px 0px 0px;
      @media (max-width: 768px) {
        margin: 20px 0px 0px 0px;
        font-size: 16px;
      }
    }
  }

  // .block-inner__right

  &__right {
    flex: 0 1 287px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 20px;
    @media (max-width: 992px) {
      align-items: start;
    }
    p {
      display: flex;
      flex-direction: column;
      flex: 1 1 auto;
      font-weight: 350;
      font-size: 20px;
      line-height: 160%;
      color: #fff;
      a {
        text-decoration: underline;
        transition: opacity 0.3s;
        &:hover {
          opacity: 0.8;
        }
      }
      @media (max-width: 768px) {
        font-size: 16px;
      }
    }
    img {
      width: 45px;
      height: 45px;
      display: block;
      @media (max-width: 768px) {
        width: 35px;
        height: 35px;
      }
    }
    @media (max-width: 992px) {
      flex: 1 1 auto;
      max-width: 287px;
    }
  }

  // .block-inner__name

  &__name {
    font-weight: 500;
    font-size: 20px;
    line-height: 160%;
    color: #fff;
    margin: 0px 0px 3px 0px;
    @media (max-width: 768px) {
      font-size: 16px;
    }
  }

  // .block-inner__subname

  &__subname {
    font-weight: 350;
    font-size: 15px;
    line-height: 160%;
    color: #fff;
    @media (max-width: 768px) {
      font-size: 13px;
    }
  }
}

