.block-work {
  // .block-work__content

  &__content {
  }

  // .block-work__body

  &__body {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    @media (max-width: 992px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 768px) {
      gap: 15px;
    }
    @media (max-width: 480px) {
      grid-template-columns: 1fr;
    }
  }

  // .block-work__column

  &__column {
    display: flex;
    flex-direction: column;
    height: 100%;
    background-color: var(--theme-color-grey);
    p {
      flex: 1 1 auto;
      padding: 24px;
      line-height: 160%;
      color: #111;
      @media (max-width: 768px) {
        font-size: 14px;
        padding: 15px;
      }
    }
  }

  // .block-work__image

  &__image {
    position: relative;
    height: 118px;
    @media (max-width: 768px) {
      height: 100px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  // .block-work__number

  &__number {
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 24px;
    left: 24px;
    width: calc(100% - 24px);
    @media (max-width: 768px) {
      bottom: 15px;
      left: 15px;
      width: calc(100% - 15px);
    }
    span {
      flex: 0 0 40px;
      background: #fff;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      font-weight: 500;
      font-size: 14px;
      line-height: 160%;
      text-transform: uppercase;
      text-align: center;
      color: var(--theme-color-accent);
      display: flex;
      justify-content: center;
      align-items: center;
      @media (max-width: 768px) {
        flex: 0 0 30px;
        width: 30px;
        height: 30px;
        font-size: 12px;
      }
    }
    &::after {
      content: "";
      width: 100%;
      border: 1px dashed rgba(255, 255, 255, 0.5);
      margin: 0px 0px 0px 16px;
    }
  }
}

.block-projects {
  padding: 120px 0px;
  background-color: var(--theme-color-accent);
  @media (max-width: 768px) {
    padding: 60px 0px;
  }
  .heading-complex1 {
    color: #fff;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  }

  // .block-projects__content

  &__content {
    .container {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  // .block-projects__body

  &__body {
    width: 100%;
    display: grid;
    gap: 30px;
    grid-template-columns: repeat(3, 1fr);
    @media (max-width: 1200px) {
      gap: 20px;
    }
    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  // .block-projects__column

  &__column {
    height: 100%;
    display: flex;
    flex-direction: column;
    color: #fff;
    transition:
      background-color 0.3s ease,
      color 0.3s ease;

    &.item-active {
      background-color: #fff;
      color: var(--theme-color-text-common);
      ul {
        li {
          span {
            color: #7c7c7c;
          }
        }
      }
      .block-projects__bottom {
        opacity: 1;
      }
    }
    &:hover {
      background-color: #fff;
      color: var(--theme-color-text-common);
      ul {
        li {
          span {
            color: #7c7c7c;
          }
        }
      }
      .block-projects__bottom {
        opacity: 1;
      }
    }
  }

  // .block-projects__image

  &__image {
    display: block;
    height: 261px;
    width: 100%;
    object-fit: cover;
    @media (max-width: 480px) {
      height: 200px;
    }
  }

  // .block-projects__item

  &__item {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 24px;
    @media (max-width: 1200px) {
      padding: 15px;
    }
    @media (max-width: 992px) {
      height: auto;
    }
    ul {
      flex: 1 1 auto;
      display: flex;
      flex-direction: column;
      gap: 4px;
      li {
        font-weight: 350;
        font-size: 15px;
        line-height: 150%;
        @media (max-width: 768px) {
          font-size: 13px;
        }
        span {
          transition: color 0.3s ease;
          color: rgba(255, 255, 255, 0.6);
        }
      }
    }
  }

  // .block-projects__title

  &__title {
    margin: 0px 0px 10px 0px;
  }

  // .block-projects__bottom

  &__bottom {
    opacity: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    margin: 24px 0px 0px 0px;
    transition: opacity 0.3s ease;
    @media (max-width: 768px) {
      margin: 15px 0px 0px 0px;
    }
  }

  // .block-projects__location

  &__location {
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 14px;
    line-height: 160%;
    text-transform: uppercase;
    text-align: center;
    color: #111;
    img {
      width: 18px;
      height: 18px;
      margin: 0px 6px 0px 0px;
    }
  }

  // .block-projects__arrow

  &__arrow {
    width: 34px;
    height: 34px;
  }

  // .block-projects__button

  &__button {
    margin: 60px 0px 0px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #fff;
    border-radius: 200px;
    padding: 18px 28px 17px 28px;
    font-weight: 700;
    font-size: 14px;
    line-height: 160%;
    text-transform: uppercase;
    text-align: center;
    color: #fff;
    transition: opacity 0.3s;
    &:hover {
      opacity: 0.7;
    }
    @media (max-width: 768px) {
      margin: 30px 0px 0px 0px;
      padding: 10px 28px;
    }
    @media (max-width: 480px) {
      width: 100%;
    }
  }
}

