.tabs {
  display: flex;
  flex-direction: column;
  gap: 30px 0px;
  @media (max-width: 768px) {
    gap: 15px;
  }
  // .tabs__navigation

  &__navigation {
    background: #fff;
    padding: 40px;
    @media (max-width: 1400px) {
      margin: 0px -15px;
    }
    @media (max-width: 768px) {
      padding: 15px;
    }
  }

  // .tabs__name

  &__name {
    font-weight: 500;
    font-size: 25px;
    line-height: 130%;
    color: #111;
    margin: 0px 0px 30px 0px;
    @media (max-width: 768px) {
      font-size: 20px;
      margin: 0px 0px 15px 0px;
    }
    @media (max-width: 480px) {
      font-size: 16px;
    }
  }

  // .tabs__titles

  &__titles {
    display: flex;
    flex-wrap: nowrap;
    margin: 0px -40px;
    padding: 0px 40px;
    gap: 30px;
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
      display: none;
    }
    @media (max-width: 768px) {
      margin: 0px -15px;
      padding: 0px 15px;
      gap: 15px;
    }
  }

  // .tabs__title

  &__title {
    flex: 1 1 234px;
    min-width: 234px;
    position: relative;
    color: #111;
    padding: 24px 4px 20px 24px;
    background-color: var(--theme-color-grey);
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    transition:
      background-color 0.3s,
      color 0.3s;
    @media (max-width: 768px) {
      flex: 0 0 180px;
      min-width: 180px;
      padding: 10px 4px 10px 10px;
    }
    span {
      font-weight: 500;
      font-size: 20px;
      line-height: 160%;
      width: 100%;
      text-align: left;
      display: block;
      @media (max-width: 768px) {
        font-size: 16px;
      }
      @media (max-width: 480px) {
        font-size: 14px;
      }
    }
    &._tab-active {
      background-color: var(--theme-color-accent);
      color: #fff;
      .tabs__arrow {
        opacity: 1;
      }
    }
    &:hover {
      background-color: var(--theme-color-accent);
      color: #fff;
      .tabs__arrow {
        opacity: 1;
      }
    }
  }

  // .tabs__arrow

  &__arrow {
    transition: opacity 0.3s ease;
    width: 34px;
    height: 34px;
    position: absolute;
    bottom: 24px;
    left: 24px;
    opacity: 0;
    @media (max-width: 768px) {
      left: 10px;
      bottom: 10px;
      width: 28px;
      height: 28px;
    }
  }

  // .tabs__image

  &__image {
    width: 150px;
    height: 150px;
    object-fit: contain;
    margin: 5px 0px 0px 0px;
    @media (max-width: 768px) {
      width: 100px;
      height: 100px;
    }
  }

  // .tabs__content

  &__content {
  }

  // .tabs__body

  &__body {
  }

  // .tabs__cards

  &__cards {
  }
}
