      width: 100%;
      height: 100%;
      object-fit: contain;
    }
    @media (max-width: 768px) {
      flex: 1 0 120px;
    }
    @media (max-width: 480px) {
      flex: 0 0 150px;
      height: 150px;
    }
  }

  // .block-reviews__text

  &__text {
    border: 1px solid #f7f7f7;
    padding: 24px 24px 30px 24px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 43px;
    @media (max-width: 768px) {
      padding: 15px;
      gap: 20px;
    }
    p {
      display: flex;
      flex-direction: column;
      font-weight: 350;
      font-size: 15px;
      line-height: 160%;
      color: #111;
      span {
        font-weight: 500;
      }
      @media (max-width: 768px) {
        font-size: 13px;
      }
    }
  }

  // .block-reviews__nav

  &__nav {
    margin: 40px 0px 0px 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 768px) {
      margin: 30px 0px 0px 0px;
    }
  }

  // .block-reviews__dotts

  &__dotts {
    width: 120px;
    display: flex;
    transition: background-color 0.3s;
    .swiper-pagination-bullet {
      transition: background-color 0.3s;
      cursor: pointer;
      flex: 1 1 auto;
      opacity: 1;
      height: 3px;
      border-radius: 0px;
      background-color: var(--theme-color-grey);
      &:hover {
        background-color: rgba(47, 128, 237, 0.5);
      }
    }
    .swiper-pagination-bullet-active {
      background-color: var(--theme-color-accent);
    }
  }

  // .block-reviews__fraction

  &__fraction {
    display: flex;
    align-items: center;
    gap: 27px;
    font-weight: 700;
    font-size: 14px;
    line-height: 160%;
    text-transform: uppercase;
    color: #111;
    @media (max-width: 768px) {
      gap: 15px;
      font-size: 12px;
    }
  }

  // .block-reviews__current

  &__current {
  }

  // .block-reviews__all

  &__all {
  }

  // .block-reviews__arrows

  &__arrows {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  // .block-reviews__arrow-prev

  &__arrow-prev {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    background-color: var(--theme-color-accent);
    transition: background-color 0.3s;
    img {
      width: 18px;
      height: 12px;
      transform: rotate(-180deg);
    }
    &:hover {
      background-color: var(--theme-color-accent-80);
    }
    &.swiper-button-disabled {
      pointer-events: none;
      opacity: 0.8;
    }
    @media (max-width: 768px) {
      display: none;
    }
  }

  // .block-reviews__arrow-next

  &__arrow-next {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    background-color: var(--theme-color-accent);
    transition: background-color 0.3s;
    img {
      width: 18px;
      height: 12px;
    }
    &:hover {
      background-color: var(--theme-color-accent-80);
    }
    &.swiper-button-disabled {
      pointer-events: none;
      opacity: 0.8;
    }
    @media (max-width: 768px) {
      display: none;
    }
  }
}

.block-guarantee {
  // .block-guarantee__body

  &__body {
    display: flex;
    gap: 20px;
    margin: 0px 10px 0px 27px;
    @media (max-width: 992px) {
      flex-direction: column;
      margin: 0px 0px 0px 0px;
    }
    .title2 {
      flex: 1 1 55%;
      @media (max-width: 992px) {
        flex: 1 1 auto;
      }
    }
  }

  // .block-guarantee__right

  &__right {
    display: flex;
    flex: 0 1 51%;
    font-weight: 350;
    font-size: 18px;
    @media (max-width: 992px) {
      flex: 1 1 auto;
    }
    img {
      width: 60px;
      height: 60px;
      margin: 1px 30px 0px 0px;
      @media (max-width: 768px) {
        width: 40px;
        height: 40px;
        margin: 8px 20px 0px 0px;
      }
    }
    @media (max-width: 768px) {
      font-size: 14px;
    }
  }
}
